import React, { Component, Fragment } from "react";
import Navbar from '../../components/Navbar/Navbar'
import { MDBContainer, MDBInput } from "mdbreact";
import Multislider from '../../components/Multislider/Multislider';
import Footer from '../../components/Footer/Footer';
import './Cart.css';
import Grid from '../../components/Grid/Grid';
import ScrollUpButton from "react-scroll-up-button"; //Add this line Here
import Productsadded from '../../components/Productsadded/Productsadded';
import { Link } from 'react-router-dom';

class Cart extends Component {



    showtab(cityName) {
        var i, tabcontent, tablinks;
        tabcontent = document.getElementsByClassName("tabcontent");
        for (i = 0; i < tabcontent.length; i++) {
            tabcontent[i].style.display = "none";
        }
        tablinks = document.getElementsByClassName("tablinks");
        for (i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" active", "");
        }
        document.getElementById(cityName).style.display = "block";

    }

    render() {
        const imgstyle = { maxWidth: "100px", margin: "20px", maxHeight: "100px" }

        return (

            <Fragment>

                <ScrollUpButton />

                <Navbar></Navbar>


                <MDBContainer fluid className="home">
                    <div style={{margin:"20px"}}>
                        <Productsadded type="cart">

                        </Productsadded>
                    </div>

                    <div style={{ marginTop: "100px" }}>
                        <h4 style={{ margin: "5px" }}>Other items you might be interested in</h4>

                        <div style={{ background: "white" }}>

                            <Multislider sponsers={false} picheight={"150px"} height={"250px"} width={"200px"} numbers={4}>

                            </Multislider>
                        </div>
                    </div>
                    <div style={{ marginTop: "50px" }}>

                        <div className="col-md-12">

                            <div className="tab">
                                <button className="tablinks active" onClick={() => this.showtab('itemdesc')}>New Arrivals</button>
                                <button className="tablinks" onClick={() => this.showtab('reviews')}>Bestselling</button>
                                <button className="tablinks" onClick={() => this.showtab('shoppingtime')}>Super Deals</button>
                            </div>

                            <div id="itemdesc" style={{ display: "block" }} className="tabcontent">
                                <Grid>

                                </Grid>
                            </div>

                            <div id="reviews" className="tabcontent">
                                <Grid>

                                </Grid>
                                <Grid>

                                </Grid>    </div>

                            <div id="shoppingtime" className="tabcontent">
                                <Grid>

                                </Grid>
                            </div>

                        </div>

                    </div>


                </MDBContainer>

                <Footer></Footer>
            </Fragment >

        );
    }
}

export default Cart;